let vh = window.innerHeight * 0.01;
document.documentElement.style.setProperty("--vh", `${vh}px`);

import "bootstrap";
import {Splide} from "@splidejs/splide";
import ClipboardJS from "clipboard";
import {Toast} from "bootstrap";

var mapOptions = {
    center: new naver.maps.LatLng(37.4822436,127.0355851),
    zoom: 16,
};
var map = new naver.maps.Map('map', mapOptions);

var ico_pin = new URL('../assets/ico_pin.jpg', import.meta.url);
var marker = new naver.maps.Marker({
    position: position,
    map: map,
    icon: {
        url: ico_pin,
        size: new naver.maps.Size(25, 34),
        scaledSize: new naver.maps.Size(25, 34),
        origin: new naver.maps.Point(0, 0),
        anchor: new naver.maps.Point(12, 34)
    }
});

document.addEventListener('DOMContentLoaded', function () {
    new ClipboardJS('.acc');

    var tt = document.getElementById('accToast');
    document.querySelectorAll('.acc').forEach(
        (val, key, par) => {
            val.addEventListener('click', function () {
                var toast = new Toast(tt);
                toast.show()
            })
        }
    );

    var targetDate = new Date('06/11/2022');
    var days = Math.max(0, targetDate.getTime() - new Date().getTime() + 1000 * 60 * 60 * 24 - 1) / (1000 * 60 * 60 * 24);
    document.getElementById('countdown-day')?.textContent = Math.floor(days) + "";
    
    var thumb = new Splide( '#thumbnail-carousel', {
		fixedWidth: 70,
        fixedHeight: 70,
		gap       : 10,
		rewind    : true,
		pagination: false,
        isNavigation: true,
        arrows    : false,
        focus      : 'center',
        breakpoints: {
          600: {
            fixedWidth : 50,
            fixedHeight: 50,
          },
        },
    });

    var splide = new Splide( '#main-carousel', {
        pagination: false,
        arrows    : false,
        lazyLoad: 'nearby',
    });
    var thumbnails = document.getElementsByClassName('thumbnail');
    for (let i = 0; i < thumbnails.length; i++) {
        thumbnails[i].addEventListener('click', function () {
            splide.go(i);
        });
    }
    var current: Element | null = null;
    splide.on('mounted move', function () {
        var thumbnail = thumbnails[splide.index];
        if ( thumbnail ) {
            if ( current ) {
                current.classList.remove( 'is-active' );
            }
            thumbnail.classList.add( 'is-active' );
            current = thumbnail;
        }
    } );
    splide.sync(thumb);
    splide.mount();
    thumb.mount();
});